body,
html {
  height: 100%;
  margin: 0;
  font-family: "Nunito Sans", sans-serif;
}

* {
  box-sizing: border-box;
}

.bg-image {
  filter: blur(8px);
  -webkit-filter: blur(8px);
  height: 400px;
  background-position: inherit;
  background-repeat: no-repeat;
  background-size: cover;
}
.bg-text {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.4),rgba(0, 0, 0, 0.4), transparent);
  color: white;
  font-weight: bold;
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
  z-index: 2;
  width: 100%;
  height: 400px;
  text-align: left;
}
.bg-text-cont {
  padding-top: 50px;
}
.title {
  font-size: 80px;
}
@media (max-width: 670px) {
  .bg-text {
    width: 100%;
    height: 500px;
  }
  .bg-image {
    height: 500px;
  }
  .title {
    font-size: 50px;
  }
}

/*https://res.cloudinary.com/dmcbeyvr4/image/upload/v1628275669/iitgstackoverflow/iitgback_ita5se.jpg*/

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Nunito Sans", sans-serif;
}

.page-manual {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f1f1f1;
}

.card-manual {
  width: 75%;
  height: 70%;
  background-color: white;
  box-shadow: 0px 50px 100px rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
}

.container-manual {
  height: 100%;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.menu-manual {
  width: 100%;
  height: 10%;
}

.menu-manual h3 {
  font-size: 1em;
  margin-left: 25px;
  font-weight: 800;
  float: left;
}
.content-manual {
  width: 90%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content-manual .text {
  margin-bottom: 50px;
}

.text-manual h1 {
  font-size: 2.5em;
  line-height: 1;
  font-weight: 700;
}

.text-manual p {
  color: rgb(0, 0, 0, 0.5);
  margin-bottom: 40px;
  margin-top: -5px;
}

.text-manual button {
  font-weight: 600;
  color: white;
  text-decoration: none;
  background-color: #fe864c;
  transition: background-color 0.2s;
  transition: color 0.4s;
  padding: 10px 20px;
  border: 2px solid #fe864c;
}

.text-manual button:hover,
.text button:active {
  background-color: white;
  color: #fe864c;
}

.photo-manual {
  height: 100%;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("https://res.cloudinary.com/dmcbeyvr4/image/upload/v1628275669/iitgstackoverflow/iitgback_ita5se.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.iitgicon-manual {
  width: 100px;
  padding-top: 5%;
  padding-left: 5%;
}
@media (max-width: 670px) {
  .page-manual {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #f1f1f1;
  }
  .menu-manual {
    display: none;
  }
  .card-manual {
    height: 100%;
    width: 100%;
    flex-direction: column;
  }
  .photo-manual {
    width: 100%;
  }
  .container-manual {
    width: 90%;
  }
  .iitgicon-manual {
    width: 70px;
  }
}

